const WxAuth = require('@/config/WxAuth');
export default {
	data() {
		return {
			isClient: true, // 是否是用户
			mobile: {
				phone: '',
				code: ''
			},
			// 验证码
			safety: {
				time: 60,
				state: false,
				interval: ''
			},
			loginMobile: false,
			phoneReg: /^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/,
		}
	},
	watch: {
		/**
		 * 监听手机登录数值
		 */
		mobile: {
			handler(newValue) {
				if (this.phoneReg.test(newValue.phone) && newValue.code.length === 6) {
					this.loginMobile = true;
				} else {
					this.loginMobile = false;
				}
			},
			deep: true
		},
	},
	onLoad(options) {
		if (options.login && options.login == "shop") {
			this.isClient = false
		}
		console.log("option", options)
		console.log("this.isClient", this.isClient)
	},
	methods: {
		// 登录按钮点击执行
		fnLogin() {
			console.log(JSON.stringify(this.mobile));
			uni.showLoading({
				title: "登陆中...",
				mask: true
			})
			setTimeout(() => {
				uni.hideLoading()
				if (this.isClient) {
					uni.switchTab({
						url: "/pages/index/index"
					})
				}
			}, 2000)


		},
		// 获取用户信息
		getUserInfo() {

		},
		// 获取用户电话
		getPhoneNumber(e) {
			console.log(e)
			let {
				errMsg,
				iv,
				encryptedData
			} = e.detail;
			let confirm_unionid = uni.getStorageSync('confirm_unionid') || '';
			if (errMsg == "getPhoneNumber:ok") { // 同意授权
				uni.hideLoading()
				// 检查登录态是否过期。
				WxAuth.checkSession().then(code => {
					//code, iv, encryptedData 传给服务器解析，得到手机号信息
					const data = {
						code,
						iv,
						encryptedData,
						confirm_unionid
					};
					// token
					this.$http.getUnionid(data, res => {
						console.log("getUnionid", res)
						// uni.showToast({
						// 	title: "授权成功！"
						// })
						// uni.hideLoading();
					})
					// api.wxmobile(data)
					// 	.then(res => {
					// 		uni.showToast({
					// 			title: '授权成功！'
					// 		});
					// 		this.process.phone = 1;
					// 		uni.hideLoading();
					// 		setTimeout(function() {
					// 			uni.navigateBack({
					// 				delta: 1 //上一级页面
					// 			});
					// 		}, 1000);
					// 	}).catch(err => {
					// 		console.log(err)
					// 	});
				}).catch(console.log)
			} else {
				// 拒绝手机号授权
				uni.showToast({
					title: '手机号授权失败！',
					icon: 'none'
				});
			}
		},
		// 获取验证码
		fnGetPhoneCode() {
			if (this.phoneReg.test(this.mobile.phone)) {
				uni.showToast({
					title: "正在发送验证码",
					icon: "loading",
					success: () => {
						// 成功后显示倒计时60s后可在点击
						this.safety.state = true;
						// 倒计时
						this.safety.interval = setInterval(() => {
							if (this.safety.time-- <= 0) {
								this.safety.time = 60;
								this.safety.state = false;
								clearInterval(this.safety.interval);
							}
						}, 1000);
						uni.showToast({
							title: "发送成功",
							icon: "success"
						})
					}
				})
			} else {
				uni.showToast({
					title: "手机号不正确",
					icon: "none"
				})
			}
		},
		// 实名认证
		switchAuthen() {
			uni.navigateTo({
				url: "/pages/login/authentication"
			})
		},
	}
}
